import React from "react";
import Modal from "react-modal";
import { toast } from "react-toastify";
import closeIcon from "~/assets/images/closeIcon.png";
import { directusLoaderLink } from "~/utils/constants.js";
import { range } from "lodash";
import { addDays, format, getDay } from "date-fns";
if (typeof window === "undefined") {
} else {
  var ReactDataGrid = require("react-data-grid");
  const defaultParsePaste = (str) =>
    str.split(/\r\n|\n|\r/).map((row) => row.split("\t"));

  function TableDataModal(prop) {
    const { Details, setIsOpenv } = prop;
    const { data, foodWaste } = Details;
    const [modalIsOpen, setIsOpen] = React.useState(setIsOpenv ? true : false);
    const [preventDouble, setPreventDouble] = React.useState(false);

    const columns = [
      {
        key: "day",
        name: "Week Day",
        editable: false
      },
      {
        key: "no_users",
        name: "Number of users on that day",
        editable: true,
      },
      {
        key: "palte_waste",
        name: "Plate Waste",
        editable: true,
      },
      {
        key: "total_waste",
        name: "Total Waste",
        editable: true,
      },
    ];

    const initialRows = [
      {
        day: "Monday",
        dayNo: 1,
        no_users: "",
        palte_waste: "",
        total_waste: "",
        editable: true,
        ID: "",
      },
      {
        day: "Tuesday",
        dayNo: 2,
        no_users: "",
        palte_waste: "",
        total_waste: "",
        editable: true,
        ID: "",
      },
      {
        day: "Wednesday",
        dayNo: 3,
        no_users: "",
        palte_waste: "",
        total_waste: "",
        editable: true,
        ID: "",
      },
      {
        day: "Thursday",
        dayNo: 4,
        no_users: "",
        palte_waste: "",
        total_waste: "",
        editable: true,
        ID: "",
      },
      {
        day: "Friday",
        dayNo: 5,
        no_users: "",
        palte_waste: "",
        total_waste: "",
        editable: true,
        ID: "",
      },
    ];

    foodWaste?.length > 0 &&
      foodWaste.map((e) => {
        const ind = initialRows.findIndex(
          (obj) => obj.dayNo === getDay(new Date(e?.date))
        );
        initialRows[ind].no_users = e?.number_of_users;
        initialRows[ind].palte_waste = e?.food_waste;
        initialRows[ind].total_waste = e?.total_waste;
        initialRows[ind].ID = `${e?.id}`;
        return { day: getDay(new Date(e?.date)), date: e?.date, ind: ind };
      });

    const [state, setState] = React.useState({
      rows: initialRows,
      topLeft: {},
      botRight: {},
    });
    const directus = directusLoaderLink.items("Food_waste_information");
    const closeModal = () => {
      setIsOpenv(false);
      setIsOpen(false);
    };
    const customStyles = {
      content: {
        top: "50%",
        left: "50%",
        right: "auto",
        bottom: "auto",
        marginRight: "-50%",
        transform: "translate(-50%, -50%)",
        minWidth: "300px",
      },
    };
    const submitHandle = async () => {
      setPreventDouble(true);
      var errorFlag = false;
      for (var [i, e] of rows?.entries()) {
        if (e?.ID?.trim()?.length === 0) {
          // id not found scenarios
          (e?.no_users?.trim()?.length > 0 ||
            e?.palte_waste?.trim()?.length > 0 ||
            e?.total_waste?.trim()?.length > 0) &&
            (await directus
              ?.createOne({
                date: addDays(new Date(data?.startDate), i + 1),
                location_id: data?.LocationId,
                number_of_users:
                  e?.no_users?.trim()?.length > 0 ? e?.no_users : 0,
                food_waste:
                  e?.palte_waste?.trim()?.length > 0 ? e?.palte_waste : 0,
                total_waste:
                  e?.total_waste?.trim()?.length > 0 ? e?.total_waste : 0,
              })
              .then(async (re) => { })
              .catch((e) => {
                errorFlag = true;
                console.log(e, "error");
              }));
        } else {
          e?.ID &&
            (await directus
              ?.updateOne(Number(e?.ID), {
                number_of_users:
                  typeof e?.no_users === "string"
                    ? e?.no_users?.trim().length > 0
                      ? e?.no_users
                      : 0
                    : e?.no_users > 0
                      ? e?.no_users
                      : 0,
                food_waste:
                  typeof e?.palte_waste === "string"
                    ? e?.palte_waste?.trim().length > 0
                      ? e?.palte_waste
                      : 0
                    : e?.palte_waste > 0
                      ? e?.palte_waste
                      : 0,
                total_waste:
                  typeof e?.total_waste === "string"
                    ? e?.total_waste?.trim().length > 0
                      ? e?.total_waste
                      : 0
                    : e?.total_waste > 0
                      ? e?.total_waste
                      : 0,
              })
              .then(async (re) => { })
              .catch((e) => {
                console.log(e);
                errorFlag = true;
              }));
        }
      }
      if (errorFlag === true) {
        toast.error("Something went wrong");
      } else {
        closeModal();
        localStorage.setItem("foodWasteReload", true);
        toast.success("Updated successfully");
      }
      setPreventDouble(false);
    };

    React.useEffect(() => {
      document.addEventListener("copy", handleCopy);
      document.addEventListener("paste", handlePaste);
      return () => {
        document.removeEventListener("copy", handleCopy);
        document.removeEventListener("paste", handlePaste);
      };
    }, [state]);

    const rowGetter = (i) => {
      const { rows } = state;
      return rows[i];
    };

    const updateRows = (startIdx, newRows) => {
      setState((state) => {
        const rows = state.rows.slice();
        for (let i = 0; i < newRows?.length; i++) {
          if (startIdx + i < rows?.length) {
            rows[startIdx + i] = { ...rows[startIdx + i], ...newRows[i] };
          }
        }
        return { rows };
      });
    };

    const handleCopy = (e) => {
      e.preventDefault();
      const { topLeft, botRight } = state;
      // Loop through each row
      const text = range(topLeft.rowIdx, botRight.rowIdx + 1)
        .map(
          // Loop through each column
          (rowIdx) =>
            columns
              .slice(topLeft.colIdx, botRight.colIdx + 1)
              .map(
                // Grab the row values and make a text string
                (col) => rowGetter(rowIdx)[col.key]
              )
              .join("\t")
        )
        .join("\n");
      e.clipboardData.setData("text/plain", text);
    };

    const handlePaste = (e) => {
      e.preventDefault();
      const { topLeft } = state;
      const newRows = [];
      const pasteData = defaultParsePaste(
        e.clipboardData.getData("text/plain")
      );
      const pasteDataIsNum = [];
      pasteData.forEach((row) => {
        const rowData = {};
        // Merge the values from pasting and the keys from the columns
        const start = topLeft.colIdx == 0 ? 1 : topLeft.colIdx;
        columns.slice(start, start + row?.length).forEach((col, j) => {
          // Create the key-value pair for the row
          if (col.key !== "day") {
            rowData[col.key] = row[j];
            pasteDataIsNum.push(isNaN(row[j]) === false);
          }
        });
        // is number check
        // Push the new row to the changes
        newRows.push(rowData);
      });
      if (pasteDataIsNum?.includes(false) !== true) {
        updateRows(topLeft.rowIdx, newRows);
      } else {
        setPreventDouble(true);
        toast.error("Paste valid digit values");
        setTimeout(() => {
          setPreventDouble(false);
        }, 1000);
      }
    };

    const onGridRowsUpdated = ({ fromRow, toRow, updated, action }) => {
      if (Object.keys(updated)?.[0] !== "day" && action !== "COPY_PASTE") {
        if (isNaN(Object.values(updated)?.[0]) === false) {
          setState((state) => {
            const rows = state.rows.slice();
            for (let i = fromRow; i <= toRow; i++) {
              rows[i] = { ...rows[i], ...updated };
            }
            return { rows };
          });
        } else {
          setPreventDouble(true);
          toast.error("Enter valid digit");
          setTimeout(() => {
            setPreventDouble(false);
          }, 1000);
        }
      }
    };

    const setSelection = (args) => {
      setState({
        ...state,
        topLeft: {
          rowIdx: args.topLeft.rowIdx,
          colIdx: args.topLeft.idx,
        },
        botRight: {
          rowIdx: args.bottomRight.rowIdx,
          colIdx: args.bottomRight.idx,
        },
      });
    };

    const { rows } = state;
    return (
      <div className="w-100 h-100">
        <Modal
          isOpen={modalIsOpen}
          onRequestClose={closeModal}
          style={customStyles}
          contentLabel="Food Waste Modal"
          ariaHideApp={false}
        >
          <div className="food-waste-grid rounded-[10px] shadow-lgpx-6 py-4 w-full min-w-[900px] max-w-[900px]">
            <div className="close-div">
              <button onClick={closeModal}>
                <img src={closeIcon} alt="close-icon" />
              </button>
            </div>
            <h1 className="text-[18px] font-bold text-black mb-2 text-center">
              Week {data?.currentWeek}
            </h1>
            <span className="mb-3 text-center block text-black">
              {data?.weekStartAndEnd}
            </span>
            <ReactDataGrid
              columns={columns}
              rowGetter={(i) => rows[i]}
              rowsCount={rows?.length}
              onGridRowsUpdated={onGridRowsUpdated}
              enableCellSelect
              minColumnWidth={40}
              cellRangeSelection={{
                onComplete: setSelection,
              }}
              enableRowSelect={null}
            />

            <button
              type="button"
              onClick={() => preventDouble === false && submitHandle()}
              className="min-w-[112px] px-2 py-1.5 flex justify-center items-center uppercase bg-primary text-white font-semibold text-sm rounded save-btn mt-5 mr-[7px] float-right"
            >
              Submit
            </button>
          </div>
        </Modal>
      </div>
    );
  }
}

export default TableDataModal;
