import React from "react";
import { Line } from "react-chartjs-2";
import "chart.js/auto";
import { getYear } from "date-fns";
function FoodWaste({ graphPoints, AnnualWaste, year }) {
  const Labels = ["0"];
  const palteWasteDataSet = [0];
  const totalWasteDataSet = [0];
  graphPoints?.map((e, i) => {
    Labels.push(`${i + 1}`);
    palteWasteDataSet.push(e?.plate_waste);
    totalWasteDataSet.push(e?.total_waste);
  });

  const palteWasteData = {
    labels: Labels,
    datasets: [
      {
        label: "",
        data: palteWasteDataSet,
        borderColor: "#A9A8B8",
        backgroundColor: "#A9A8B8",
        // pointBorderColor: "#AAA",
        // pointBackgroundColor: "#FEF1F1",
        pointStyle: "circle",
        fill: true,
        pointRadius: 0,
        lineTension: 0.5,
      },
    ],
  };
  const totalWasteData = {
    labels: Labels,
    datasets: [
      {
        label: "",
        data: totalWasteDataSet,
        borderColor: "#A9A8B8",
        backgroundColor: "#A9A8B8",
        // pointBorderColor: "#AAA",
        // pointBackgroundColor: "#FEF1F1",
        pointStyle: "circle",
        fill: true,
        pointRadius: 0,
        lineTension: 0.5,
      },
    ],
  };
  const options = {
    responsive: true,
    bezierCurve: true,
    title: {
      display: false,
    },
    plugins: {
      legend: {
        display: false,
      },
    },
    legend: {
      display: false,
    },
    tooltips: {
      enabled: false,
    },
    scales: {
      x: {
        grid: {
          display: false,
        },
        ticks: {
          display: false,
        },
      },
      y: {
        grid: {
          display: false,
        },
        ticks: {
          display: false,
        },
      },
    },
  };
  const containerCall = ({
    title,
    val1,
    val2,
    graph,
    Increased,
    graphPresent,
  }) => {
    return (
      <div className="felx-col ">
        {(val1 > 0 || val2 > 0 || graphPresent) && (
          <div className="m-text f-2vw fs-1-6vw fw-700">{title}</div>
        )}
        <div className="flex text-center gap-5 justify-between py-2 items-end">
          {val1 > 0 && (
            <div className="items-center justify-center align-middle">
              <span className="mb-3 block text-sm font-medium">Last Week</span>
              <div
                className={`${!Increased ? "text-red-600" : "text-emerald-600"
                  } relative flex border-2 gap-1 rounded-full border-black w-[150px] max-w-[150px] h-[150px] max-h-[150px] justify-center items-center`}
              >
                <span className="text-4xl font-bold">{val1?.toFixed(2)}</span>
                <span className="font-medium flex text-3xl max-w-[18px] justify-center">
                  {!Increased ? (
                    <i className="ri-arrow-up-line ri-1x" />
                  ) : (
                    <i className="ri-arrow-down-line ri-1x" />
                  )}
                </span>
              </div>
            </div>
          )}
          {val2 > 0 && (
            <div className="items-center justify-center flex">
              <div className="max-w-[150px]">
                <span className="mb-3 block text-sm font-medium">
                  Annual <br /> Weekly Average
                </span>
                <div className="flex border-2 rounded-full border-black	w-[150px] max-w-[150px] h-[150px] max-h-[150px] justify-center items-center">
                  <span className="text-4xl font-bold">{val2}</span>
                </div>
              </div>
            </div>
          )}
          {graphPresent && (
            <div className="">
              <span className="mb-5"></span>
              <div className="">
                <Line
                  data={graph?.data}
                  options={graph?.options}
                  width={"650px"}
                />
              </div>
            </div>
          )}
        </div>
      </div>
    );
  };
  const plate_waste_graph = graphPoints?.filter(
    (e) => e?.plate_waste !== 0 && e?.plate_waste !== null
  );
  const total_waste_graph = graphPoints?.filter(
    (e) => e?.total_waste !== 0 && e?.total_waste !== null
  );
  return (
    <div className="">
      <div className="mb-5">
        {containerCall({
          title: "Plate Waste",
          val1: graphPoints !== undefined && graphPoints[1]?.plate_waste,
          val2: Number(AnnualWaste?.plate_waste)?.toFixed(2),
          Increased:
            graphPoints !== undefined &&
            graphPoints[1]?.plate_waste < graphPoints[2]?.plate_waste,
          graph: { data: palteWasteData, options: options },
          graphPresent: plate_waste_graph?.length > 0,
        })}
      </div>
      <div className="total-waste">
        {containerCall({
          title: "Total Waste",
          val1: graphPoints !== undefined && graphPoints[1]?.total_waste,
          val2: Number(AnnualWaste?.total_waste)?.toFixed(2),
          Increased:
            graphPoints !== undefined &&
            graphPoints[1]?.total_waste < graphPoints[2]?.total_waste,
          graph: { data: totalWasteData, options: options },
          graphPresent: total_waste_graph?.length > 0,
        })}
      </div>
    </div>
  );
}

export default FoodWaste;
