import React from "react";
import Modal from "react-modal";
import { toast } from "react-toastify";
import closeIcon from "~/assets/images/closeIcon.png";
import { FRONTEND_URL } from "~/utils/constants.js";
import FoodWaste from "./foodWaste";

function FoodWasteBannerModal(prop) {
  const { Details, setIsOpenv } = prop;
  const [modalIsOpen, setIsOpen] = React.useState(setIsOpenv ? true : false);
  const closeModal = () => {
    setIsOpenv(false);
    setIsOpen(false);
  };
  const CopyLink = () => {
    navigator?.clipboard?.writeText(
      `${FRONTEND_URL}/banner/foodwaste/${Details?.LocationId}`
    );
    toast.success("Link copied to clipboard!");
  };
  const customStyles = {
    content: {
      top: "50%",
      left: "50%",
      right: "auto",
      bottom: "auto",
      marginRight: "-50%",
      transform: "translate(-50%, -50%)",
      padding: "0px",
      width: "96%",
    },
  };
  return (
    <div className="w-100 h-100">
      <Modal
        isOpen={modalIsOpen}
        onRequestClose={closeModal}
        style={customStyles}
        contentLabel="Food Waste Modal"
        ariaHideApp={false}
      >
        <div className="bg-[#FAFAFA] rounded-[10px] shadow-lg px-6 py-12 w-full relative lg:px-9 station-preview">
          <div className="close-div">
            <button onClick={closeModal} className="float-right">
              <img src={closeIcon} alt="close-icon" />{" "}
            </button>
          </div>
          <div className={`banner-fluid food-waste-banner w-full`}>
            <div className="content-vh ">
              <div className={`container !w-full`}>
                <FoodWaste
                  graphPoints={Details?.graphPoints}
                  AnnualWaste={Details?.AnnualWaste}
                  year={Details?.year}
                />
              </div>
            </div>
          </div>
        </div>
        {Details?.isAdminuser && (
          <div className={`bottom-row banner-button`}>
            <button onClick={CopyLink}>
              Copy Link <i className="ri-clipboard-line"></i>
            </button>
          </div>
        )}
      </Modal>
    </div>
  );
}

export default FoodWasteBannerModal;
