import React, { useState, useRef, useEffect } from "react";
import { toast } from "react-toastify";
import { directusLoaderLink } from "../../utils/constants";
import ConfirmationModal from "../Modal/confirmationModal";
import { addDays, format } from "date-fns";
import BannerPopup from "../banner/bannerPopup";

function CellCopy(prop) {
  const stationData = prop?.station;
  const divShow =
    prop?.copyMealClickIndex != undefined &&
    prop?.copyMealClickIndex != "" &&
    prop?.childIndex == prop?.copyMealClickIndex?.key &&
    stationData?.date == prop?.copyMealClickIndex?.date &&
    stationData?.stationId == prop?.copyMealClickIndex?.sId &&
    prop?.copyMealClickIndex?.type !== "paste"
      ? true
      : false;
  const modalShow =
    prop?.copyMealClickIndex != undefined &&
    prop?.copyMealClickIndex != "" &&
    prop?.childIndex == prop?.copyMealClickIndex?.key &&
    stationData?.date == prop?.copyMealClickIndex?.date &&
    stationData?.stationId == prop?.copyMealClickIndex?.sId &&
    prop?.copyMealClickIndex?.type === "paste" &&
    prop?.copyMealClickIndex?.modal
      ? true
      : false;
  const [isDivVisible, setDivVisible] = useState(divShow);
  const [modalIsOpen, setIsOpen] = useState(modalShow);
  const [bannerModalIsOpen, setBannerIsOpen] = useState(modalShow);
  const [modalLoader, setModalLoader] = useState(false);
  const divRef = useRef(null);

  useEffect(() => {
    const handleClickOutside = (event) => {
      if (divRef.current && !divRef.current.contains(event.target)) {
        setDivVisible(false);
        prop?.CopyMealClickIndex != undefined && prop?.CopyMealClickIndex("");
      }
    };

    document.addEventListener("mousedown", handleClickOutside);

    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
    };
  }, []);
  const toggleDiv = async () => {
    const PasteData = await navigator?.clipboard
      ?.readText()
      ?.then((clipText) => {
        const arr = [
          "stationId",
          "stationUniqueId",
          "stationName",
          "locationId",
          "date",
          "contentId",
          "updateId",
        ];
        const val = arr?.map((e) => clipText?.includes(e));
        return val?.includes(false) !== true ? JSON?.parse(clipText) : "";
      });
    prop?.CopyMealClickIndex != undefined &&
      prop?.CopyMealClickIndex({
        key: prop?.childIndex,
        date: stationData?.date,
        sId: stationData?.stationId,
        modal: PasteData?.stationId === stationData?.stationId,
      });
    setDivVisible(!isDivVisible);
  };
  const CopyHandle = async (e) => {
    if (stationData?.updateId !== undefined) {
     const data = {
        stationId: stationData?.stationId,
        stationUniqueId: stationData?.stationUniqueId,
        stationName: stationData?.stationName,
        locationId: stationData?.locationId,
        date: stationData?.date,
        contentId: stationData?.contentId,
        updateId: stationData?.updateId,
        plateImg: stationData?.plateImg?.id,
      };
      navigator?.clipboard?.writeText(JSON.stringify(data));
      toast.success("Data copied to clipboard!");
    } else {
      toast.error(
        "Unable to perform Copy Meal since the respective station doesn't have a data."
      );
    }
    prop?.CopyMealClickIndex != undefined && prop?.CopyMealClickIndex("");
    setDivVisible(false);
  };

  const PasteHandle = async () => {
    setModalLoader(true);
    let PasteData = await navigator?.clipboard
      ?.readText()
      ?.then((clipText) => JSON?.parse(clipText));
    if (PasteData?.stationId === stationData?.stationId) {
      if (PasteData?.updateId !== undefined) {
        const currentStationMenuId = await directusLoaderLink
          .items("screen_display")
          .readByQuery({
            fields: ["*.*."],
            filter: {
              location_id: { _eq: stationData?.locationId },
              date: { _eq: stationData?.date },
              station_name: { _eq: stationData?.stationId },
            },
          })
          .then((e) => e?.data[0]);
        if (currentStationMenuId?.id > 0) {
          await directusLoaderLink
            .items("screen_display")
            ?.updateOne(currentStationMenuId?.id, {
              recipe_id: PasteData?.updateId,
              app_resultant_image: !!PasteData?.plateImg?PasteData?.plateImg:null,
            })
            .then((re) => {})
            .catch((e) => {
              console.log(e);
              errorFlag = true;
            });
        } else {
          await directusLoaderLink
            .items("screen_display")
            ?.createOne({
              station_name: stationData?.stationId,
              date: stationData?.date,
              location_id: stationData?.locationId,
              recipe_id: PasteData?.updateId,
              app_resultant_image:!!PasteData?.plateImg?PasteData?.plateImg:null,
            })
            .then((re) => {
              return re?.id;
            })
            .catch((e) => {
              console.log(e, "error");
            });
        }

        localStorage.setItem(
          "LastWacthedWeek",
          new Date(stationData?.date)?.getDay() == 0
            ? format(addDays(new Date(stationData?.date), -2), "yyyy-MM-dd")
            : stationData?.date
        );
      } else {
        toast.error(
          "Unable to paste the data since the copied station doesn't have any data."
        );
      }
    } else {
      toast.error(
        "Unable to perform paste meal since copy and paste station is the same date."
      );
    }
    prop.CopyMealClickIndex !== undefined && prop?.CopyMealClickIndex("");
    setIsOpen(false);
    prop?.setRefresh(!prop?.refresh);
  };
  const ModelOpenHandle = async () => {
    setDivVisible(false);
    let PasteData = await navigator?.clipboard
      ?.readText()
      ?.then((clipText) => JSON?.parse(clipText));
    prop?.CopyMealClickIndex != undefined &&
      prop?.CopyMealClickIndex({
        type: "paste",
        key: prop?.childIndex,
        date: stationData?.date,
        sId: stationData?.stationId,
        modal: PasteData?.stationId === stationData?.stationId,
      });
    if (PasteData?.date !== stationData?.date) {
      PasteData?.stationUniqueId === stationData?.stationUniqueId
        ? setIsOpen(true)
        : toast.error(
            "Unable to perform paste meal due to different station selection"
          );
    } else {
      toast.error(
        "Unable to perform paste meal since copy and paste station is the same date."
      );
    }
  };
  return (
    <div className=" ">
      <i
        onClick={toggleDiv}
        className="cursor-pointer  ri-more-2-fill right-0 "
      />
      {isDivVisible && (
        <div className="div-block" ref={divRef}>
          <div
            className={`
          flex flex-col p-4 font-normal absolute min-w-[150px] bg-[#f4f4f4] border-1 right-0 z-10 gap-3 text-sm meal-popup`}
          >
            <div
              onClick={() => CopyHandle()}
              className="font-normal cursor-pointer text-black"
            >
              Copy meal
            </div>{" "}
            <div
              onClick={() => ModelOpenHandle()}
              className="font-normal cursor-pointer text-black"
            >
              Paste meal
            </div>
            <div
              onClick={() => {
                setDivVisible(false);
                // prop?.CopyMealClickIndex != undefined &&
                //   prop?.CopyMealClickIndex("");
                setBannerIsOpen(true);
              }}
              className="font-normal cursor-pointer text-black"
            >
              Preview slide
            </div>
          </div>
        </div>
      )}
      {modalIsOpen && (
        <ConfirmationModal
          modalIsOpen={modalIsOpen}
          setIsOpen={setIsOpen}
          content="Are you sure to paste station data?"
          callFunction={PasteHandle}
          CopyMealClickIndex={prop?.CopyMealClickIndex}
          Loader={modalLoader}
        />
      )}
      {bannerModalIsOpen && (
        <BannerPopup
          modalIsOpenParent={bannerModalIsOpen}
          setIsOpenParent={setBannerIsOpen}
          data={stationData}
          isAdminUser={prop?.isAdminUser}
          calorie={stationData?.calorie}
          co2={stationData?.co2}
        />
      )}
    </div>
  );
}

export default CellCopy;
