import React, { useState } from "react";
import Modal from "react-modal";
import { Carousel } from "react-responsive-carousel";
import { toast } from "react-toastify";
import { FRONTEND_URL } from "~/utils/constants.js";
import closeIcon from "~/assets/images/closeIcon.png";
import LunchContent from "./lunchContent";

function LunchBanner(props) {
  const { setIsOpenParent, data, isAdminUser, modalIsOpenParent, LocationId } =
    props;
  const [modalIsOpen, setIsOpen] = useState(
    modalIsOpenParent === true ? true : false
  );
  const customStyles = {
    overlay: {
      zIndex: 12,
    },
    content: {
      top: "50%",
      left: "50%",
      right: "auto",
      bottom: "auto",
      marginRight: "-50%",
      transform: "translate(-50%, -50%)",
      padding: "0px",
      width: "96%",
      maxWidth: "1200px",
      maxHeight: "94vh",
    },
  };
  const copyText = ({ language, type }) => {
    navigator?.clipboard?.writeText(
      `${FRONTEND_URL}/banner/today-menu/${LocationId}/${language}/${type}`
    );
    toast.success("Link copied to clipboard!");
  };
  const closeModal = () => {
    setIsOpenParent(false);
    setIsOpen(false);
  };
  const arr = [];
  const renderSingleItem = ({ language, Value, part }) => {
    arr.push(
      <LunchContent
        copyText={copyText}
        language={language}
        isAdminUser={isAdminUser}
        data={Value}
        key={Math.random()}
        part={part}
        LocationId={LocationId}
      />
    );
  };
  const renderItems = () => {
    for (let m = 0; m < 2; m++) {
      const lang = m === 0 ? "da" : "en";
      for (
        let i = 0, j = 1;
        i < (data?.length > 0 ? data?.length : 1);
        i = i + 5, j++
      ) {
        renderSingleItem({
          language: lang,
          Value: data?.length > 0 ? data?.slice(i, j * 5) : [],
          part: j,
        });
      }
    }
    return arr;
  };
  return (
    <div className="w-100,h-100">
      <Modal
        isOpen={modalIsOpen}
        onRequestClose={closeModal}
        style={customStyles}
        ariaHideApp={false}
        contentLabel="Banner Preview"
      >
        <div className="w-full min-h-[86%] lunch-preview">
          <div className="close-div">
            <button onClick={closeModal} className="float-right">
              <img src={closeIcon} alt="close-icon" />
            </button>
          </div>
          <Carousel
            showArrows={true}
            useKeyboardArrows
            autoPlay={true}
            interval={15000}
            showThumbs={false}
          >
            {renderItems()}
          </Carousel>
        </div>
      </Modal>
    </div>
  );
}

export default LunchBanner;
