import { useNavigate } from "@remix-run/react";
import React from "react";
import Modal from "react-modal";
import { toast } from "react-toastify";
import closeIcon from "~/assets/images/closeIcon.png";
import { directusLoaderLink } from "~/utils/constants.js";

export default function StationModel(prop) {
  const { stationDetail, setIsOpenv } = prop;
  const [modalIsOpen, setIsOpen] = React.useState(setIsOpenv ? true : false);
  const [stationName, setStationName] = React.useState(
    stationDetail?.stationName?.statonName
  );
  const [stationDatas, setStationDatas] = React.useState([]);

  const directus = directusLoaderLink;
  const navigate = useNavigate();

  const customStyles = {
    content: {
      top: '50%',
      left: '50%',
      right: 'auto',
      bottom: 'auto',
      marginRight: '-50%',
      transform: 'translate(-50%, -50%)',
      minWidth: '300px',
    },
  };
  const closeModal = () => {
    setIsOpenv(false);
    setIsOpen(false);
  };

  React.useEffect(() => {
    const stationDatas = async () => {
      await directus
        .items("Stations")
        .readByQuery({
          fields: ["station_name_translation"],
          filter: {
            station_unique_id: {
              _eq: stationDetail?.stationName?.StationUniqueId,
            },
            id: { _eq: stationDetail?.stationName?.StationId },
          },
        })
        .then(
          (res) =>
            res?.data[0]?.station_name_translation?.length > 0 &&
            setStationDatas(res?.data[0]?.station_name_translation)
        )
        .catch((err) => console.log(err));
    };
    stationDatas();
  }, []);

  const stationNameUpdateHandle = async () => {
    const body = stationDatas?.filter(
      (e) => e?.location_id !== stationDetail?.locationId
    );

    body.push({ location_id: stationDetail?.locationId, name: stationName });
    if (stationName?.trim()?.length > 3) {
      await directus
        .items("Stations")
        .updateOne(stationDetail?.stationName?.StationId, {
          station_name_translation: body,
        })
        .then((res) => {
          toast.success("Updated successfully");
          location?.reload();
          closeModal();
        })
        .catch((err) => {
          toast.error("Something went wrong");
          location?.reload();
          closeModal();
        });
    } else {
      toast.error("Minimum 3 letters required");
    }
  };

  return (
    <div className="w-100 h-100">
      <Modal
        isOpen={modalIsOpen}
        onRequestClose={closeModal}
        style={customStyles}
        contentLabel="Station Preview Modal"
        ariaHideApp={false}
      >
        <div className="rounded-[10px] shadow-lgpx-6 p-4 w-full">
          <div className="close-div">
            <button onClick={closeModal}>
              <img src={closeIcon} alt="close-icon" />
            </button>
          </div>
          <h1 className="text-[18px] font-bold text-black mb-5">
              Edit Station title
          </h1>
          <input
            value={stationName}
            onChange={(e) => setStationName(e?.target?.value)}
            type="text"
            id="u-mail-id1"
            name="otp"
            placeholder="enter station name"
            required
            className="mt-2 mb-5 w-full px-2 py-3 flex justify-center items-center text-black shadow-sm rounded-lg border border-gray-300 focus:border-gray-600 placeholder:font-bold placeholder:text-[#bbb] placeholder:text-[14px] focus:outline-none focus:ring-1 focus:ring-gray-600 sm:text-sm"
          />
          <button
            type="button"
            name="updateButton"
            onClick={() => stationNameUpdateHandle()}
            className="inline-flex w-full items-center justify-center h-[50px] text-[18px] rounded border bg-primary border-primary px-4 py-2 text-sm font-bold text-white shadow-sm hover:bg-primary focus:outline-none focus:ring-2 focus:ring-primary focus:ring-offset-2"
          >
            Update
          </button>
        </div>
      </Modal>
    </div>
  );
}
